@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;1,500;1,700&display=swap);
* {
  box-sizing: border-box;
}

body {
  font: 400 14px Roboto, sans-serif;
  background: #ddf3d2;
  -webkit-font-smoothing: antialized;
}

input, button, textarea {
  font: 400 18px Roboto, sans-serif;
}

button {
  cursor: pointer;
}

form input {
  width: 100%;
  height: 60px;
  color: #333;
  border: 1px solid #dcdce6;
  border-radius: 8px;
  padding: 0 24px;
}

form input[type=checkbox]:not(old),
form input[type=radio   ]:not(old){
  width   : 28px;
  height: 24px;
  margin  : 0;
  padding : 0;
  opacity : 0;
}

input[type=checkbox]:not(old) + label,
input[type=radio   ]:not(old) + label{
  display      : inline-block;
  margin-left  : -28px;
  padding-left : 28px;
  background   : url(data:image/png;base64,) no-repeat 0 0;
  line-height  : 24px;
  font-size: 18px;
  color: #494952;
  line-height: 24px;
}

input[type=checkbox]:not(old):checked + label{
  background-position : 0 -24px;
}

input[type=radio]:not(old):checked + label{
  background-position : 0 -48px;
}

.button {
  width: 100%;
  height: 60px;
  background: #23987E;
  border: 0;
  border-radius: 8px;
  color: #FFF;
  font-weight: 700;
  margin-top: 16px;
  display: inline-block;
  text-align: center;
  text-decoration: none;
  font-size: 18px;
  line-height: 60px;
  transition: -webkit-filter 0.2s;
  transition: filter 0.2s;
  transition: filter 0.2s, -webkit-filter 0.2s;
}

.button:hover {
  -webkit-filter: brightness(90%);
          filter: brightness(90%)
}
p {
  font-size: 18px;
  color: #494952;
  line-height: 24px;
  text-align: justify;
}

footer {
  margin-top: 20px;
}

footer p {
  font-size: 14px;
  color: #494952;
  line-height: 10px;
  text-align: center;
}

.tele-container {
  width: 100%;
  max-width: 1120px;
  height: 100%;
  margin: 0 auto;
  padding: 20px;

  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: center;
}

.tele-container img {
  margin-left: 40px;
  margin-right: 40px;
}

.tele-container section.form {
  width: 100%;
  max-width: 600px;
  padding: 30px;
}

.tele-container section.form form {
  margin-top: 20px;
}

.tele-container section.form form h1 {
  text-align: center;
  justify-items: center;
  font-size: 32px;
  margin-bottom: 32px;
}

.appointment-container {
  border-style: solid;
  border-width: thin;
  border-radius: 8px;
  border-color: #23987E;
  padding: 15px;
  margin-top: 20px;
  margin-bottom: 20px;
}
